import styled, { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`

    *{
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        font-family: 'Inter', sans-serif; 

        &::-moz-selection { background: #222; color: #bcbcbc;}
        &::selection { background: #222; color: #bcbcbc; }
    }    

    body {
        background-color: #bcbcbc;
        color: #222;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-family: 'Inter', sans-serif;
        height: 100%;
        width: 100%;
        -webkit-overflow-scrolling: auto;

        &::-webkit-scrollbar {
        display: none;
        }

        @media screen and (max-width: 991px) {
            background-color: #000000;
        }
    }

    h1 {
        font-family: 'Inter', sans-serif;
        font-weight: 800;
        font-size: 2.5em;
        line-height: 0.75em;
    }

    h2 {
        font-family: 'Baskervville', sans-serif;
        font-weight: 700;
        font-size: 4.5em;
    }

    h3 {
        font-family: 'Inter', sans-serif;
        font-weight: 800;
        font-size: 3.5em;
        line-height: 0.75em;
    }

    h4 {
        font-family: 'Inter', sans-serif;
        font-weight: 700;
        font-size: 1.35em;
        cursor: default;
        letter-spacing: -1px;
    }

    h5 {
        font-family: 'Inter', sans-serif;
        font-weight: 500;
        font-size: 0.9em;
    }    

    p {
        font-family: 'Inter', sans-serif;
        font-weight: 300;
        font-size: 1.25em;
        line-height: 1.25em;
    }

    .path {
        stroke-dasharray: 1000;
        stroke-dashoffset: 1000;
        animation: dash 5s linear infinite;
    }

    @keyframes cyclingColor {
        0% {
            background-color: #1e31e3;
        }
        50% {
            background-color: #ff00ff;
        }
        100% {
            background-color: #e0ae01;
        }
    }

    @media screen and (max-width: 991px) {
    }

    @keyframes dash {
        from {
            stroke-dashoffset: 2000;
        }
        to {
            stroke-dashoffset: 0;
        }
    }

    @keyframes rotation {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }  
`

// SPACERS
export const SmallSpacer = styled.div`
    height: 10px;
    cursor: default;

    @media screen and (max-width: 991px) {
        height: 8px;
    }
`

export const MediumSpacer = styled.div`
    height: 20px;
    cursor: default;

    @media screen and (max-width: 991px) {
        height: 20px;
    }
`

export const LargeSpacer = styled.div`
    height: 60px;
    cursor: default;

    @media screen and (max-width: 991px) {
        height: ${({ disableMobi }) => (disableMobi ? '0px' : '80px')};
    }
`
export const GiantSpacer = styled.div`
    height: 86vh;
    cursor: default;

    @media screen and (max-width: 991px) {
        height: 107vh;
    }
`

//CONTAINERS
//Mainly to ensure side margins
export const OverContainer = styled.div`
    top: 0;
    float: right;
    width: 50vw;
    margin-right: 25vw;
    margin-top: 4vh;
    padding-top: 4vh;
    border-top: 1px solid #222;
    z-index: 2;
    transition: all .5s ease-in-out;
    position: relative;
    
    @media screen and (max-width: 1780px) {
        width: 62vw;
        margin-right: 19vw;
    }

    @media screen and (max-width: 991px) {        
        border-radius: 10px 10px 0px 0px;
        width: 100vw;
        margin-right: 0;
        padding-top: 0;
        padding: 0 5vw;
        margin-bottom: 10vw;
        margin-top: 0;
        border-top: 0px solid #222;
        border-bottom: 0px;
        position: relative;
        margin: 0;
        margin-top: 0;
        transition: 0.5s;
    }
`

export const HomeContainer = styled(OverContainer)`    
    @media screen and (max-width: 991px) {
        background-color: #bcbcbc;
    }
`

export const FullContainer = styled(OverContainer)`
    background-color: #bcbcbc;
`

export const BlogContainer = styled(OverContainer)`
    background-color: #bcbcbc;
    width: 40vw;
    margin-right: 30vw;
    
    @media screen and (max-width: 991px) {
        width: 100vw;
        margin-right: 0;
    }
`

export const FillContainer = styled.div`
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: -4.1vh;
    left: -25vw;
    background-color: #bcbcbc;
    z-index: 999;
    overflow: hidden;
    z-index: 2;

    @media screen and (max-width: 1300px) {
        left: -19vw;
    }

    @media screen and (max-width: 991px) {
        position: absolute;
        width: 100vw;
        overflow-x: visible;        
        top: 0;
        left: 0;
        margin: -5vw;        
        margin-top: -5.1vw;
    }
`

//TEXT
//Text wrappers and containers
export const TextContainer = styled.div`
    width: 100%;
    transition: all .5s ease-in-out;
    text-align: left;
    display: inline-block; 
    vertical-align: top;
    
    @media screen and (max-width: 991px) {
        width: 100%;
        margin-left: 3px;
    }
`

export const TextWrapper = styled.div`
    text-decoration: none;
    color: #000;
    width: 100%;
    display: inline-block;
    pointer-events: all;
    padding: 0 0 0 -1px;
    background-color: ${({ backColor }) => (backColor)};
    
    @media screen and (max-width: 991px) {
        width: 100%;
    }
`

//Actual text components
export const SubText = styled.h4`
    font-size: 1em;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 1.5em;
    transition: all .2s ease-in-out;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    color: #222;
    color: ${({ customColor }) => (customColor)};
`

export const SubTitle = styled.h4` 
    color: #666;
    
    @media screen and (max-width: 991px) {
        font-size: 1em;
    }
`

export const MainText = styled(SubText)`
    font-size: 1.25em;  
    font-family: Georgia;
    font-weight: 500; 
`

//BACKDROP
//Backdrop to ensure the blend effect of the "Info" component with the name and the logo
export const Backdrop = styled.div` 
    position: absolute;
    height: 99%;
    width: 100%;
    background-color: #bcbcbc;
    transition: 0.5s;
    z-index: -2;
    margin-top: ${({ scrolled }) => (scrolled ? '-4vh' : '6vh')};

    @media screen and (max-width: 991px) {
        width: 90%;
        margin-top: ${({ scrolled }) => (scrolled ? '3vh' : '3vh')};
    }
`

//NEW BUBBLE
//Used to highlight new blog posts
export const NewBubble = styled.span`
    font-size: 8px;
    line-height: 13px;
    font-weight: 600;
    color: #222;
    vertical-align: top;
    padding: 1px 3px;
    margin-left: 0.35vh;
    max-width: 50px;
    max-height: 22px;
    border-radius: 4px;
    animation-name: cyclingColor;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    text-decoration: none;
    display: ${({ isNew }) => (isNew ? '' : 'none')};
    letter-spacing: 0px;
`

//BUBBLE TAG
//Used to provide a tag for particular project like "Mocks"
export const TagBubble = styled.span`
    font-size: 8px;
    line-height: 13px;
    font-weight: 600;
    color: #bcbcbc;
    vertical-align: top;
    padding: 1px 3px;
    margin-left: 0.35vh;
    max-width: 50px;
    max-height: 22px;
    border-radius: 4px;
    background-color: #2f2f2f;
    text-decoration: none;
    display: ${({ hasTag }) => (hasTag ? '' : 'none')};
    letter-spacing: 0px;
`

//PARAGRAPH LINK
//Used to put in a link within the paragraphs of articles
//[[ Doesn't work on blog posts yet !!! ]]
export const ParagLink = styled.a`
    font-size: 1em;  
    font-family: Georgia;
    font-weight: 500; 
    text-decoration: none;
    color: #222;
    color: #045cff;

    &:hover{
        color: #0035bf;
    }
`

export default GlobalStyle